$main-color: rgb(40, 161, 58); // Green
$text-green: rgb(14, 110, 52); // Link
$text-black: rgb(51, 51, 51); // Default
$shadow-color: rgba($main-color,0.3);
$sub-color: #ffed4d; // Yellow
$lightgray: #eff2f6; // Light gray
$phone-width: 760px;
$header-phone: 940px;
* {
 box-sizing: border-box;
 color: $text-black;
 line-height: 1.8em;
}

i.fa-solid{
 color: inherit;
 font-size: 80%;
}
a{
