.heading-balloon{
 background-color: $lightgray;
 border-radius: 4px;
 display:inline-block;
 padding: 12px 24px;
 position: relative;
 margin-top: 50px;
 margin-bottom: 40px;
 &::before{
 border-radius: 4px;
 background-color: $lightgray;
 content: '';
 display: block;
 position: absolute;
 width: 40px;
 height: 40px;
 top: calc(100% - 30px);
 left: calc(50% - 20px);
 transform: rotateZ(45deg);
 z-index: -10;
 }
}
.heading-line{
 display: inline-block;
 position: relative;
 &::before, &::after{
 content: '';
 background-color: $main-color;
 display: block;
 position: absolute;
 top: 50%;
 width: 36px;
 height: 3px;
 }
 &::before{
 margin-left: calc( -36px - 8px);
 }
 &::after{
 margin-left: calc(100% + 8px);
 }
}
