.post{
 text-align: left;
 margin: auto;
 padding-bottom: 50px;
 max-width: 800px;
 width: 80vw;
 h2{
 text-align: center;
 border-top: solid 2px $main-color;
 border-bottom: solid 2px $main-color;
 background-image: repeating-linear-gradient(-45deg, transparent, transparent 4px, rgba($main-color, 0.1) 4px, rgba($main-color, 0.1) 10px );
 margin: 4em 0 1em;
 &::before{
 content: '';
 clear: both;
