// Project Search
.search-position {
 position: relative;
}
.search-icon::before {
 display: block;
 font-family: "Font Awesome 6 Pro";
 font-weight: 900;
 content: "\f002";
}
.search-block {
 display: flex;
 align-items: center;
 background: #fff;
 margin-top: 20px;
 border-radius: 4px;
 overflow: hidden;
 width: 100%;
 opacity: 0.5;
 transition: 0.3s;

 &:focus-within {
 opacity: 1;
 box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.1);

 .search-block-input {
 opacity: 0.6;
 }
 }

 .search-block-input {
 display: flex;
 align-content: center;
 justify-content: center;
 margin: 10px 5px 10px 8px;
 box-sizing: border-box;
 opacity: 0.5;
 }

 .search-input {
 width: 100%;
 height: 60px;
 border: none;
 box-sizing: border-box;
 outline: none;
 font-size: 16px;
 background: transparent;
 }

 input::placeholder {
 font-size: 75%;
 }
}
.search-block-result {
 margin-top: 20px;
 ul {
 list-style: none;
 padding: 0;
 margin: 0 0 0 10px;
 li {
 margin-bottom: 30px;
 font-size: 80%;
 padding-left: 50px;
 text-indent: -59px;
 vertical-align: text-top;
 img {
	width: 50px;
	border-radius: 4px;
	margin-right: 10px;
 vertical-align: middle;
 }
 }
 }
}
